import React from 'react';
import { Button } from 'antd';
import http from '../../api/http.js'
function payStatus(pay_status){
    if(pay_status===0){
        return ''
    }
    if(pay_status===1){
        return ''
    }
}
function payType(pay_type){
    if(pay_type===0){
        return ''
    }
    if(pay_type===1){
        return ''
    }
    if(pay_type===2){
        return ''
    }
    if(pay_type===3){
        return ''
    }
    if(pay_type===4){
        return '()'
    }
}
function stopCarStatus(st){
    if(st===1){
        return ''
    }
    if(st===2){
        return ''
    }
    if(st===3){
        return ''
    }
    if(st===4){
        return ''
    }
}
function yeZhuStatus(st){
    if(st===1){
        return ''
    }
    if(st===2){
        return ''
    }
    if(st===3){
        return ''
    }
    if(st===4){
        return ''
    }
}
function baoXiuStatus(order_status,is_assign){
    if(order_status===2){
        if(is_assign===1){
            return (<span style={{color:'#3399FF'}}></span>)
        }
        if(is_assign===0){
            return (<span style={{color:'#F56047'}}></span>)
        }
    }
    if(order_status===3){
        return ""
    }
}
