export function matchPath(path) {
    for (let i in breadcrumb_path) {
        if (breadcrumb_path[i].path === path) {
            store.dispatch({ type: "CHANGE_BREADCRUMB", value: breadcrumb_path[i].value })
            break
        }
    }
}